import React, { useState } from 'react';

import CssBaseline from '@material-ui/core/CssBaseline';
import { makeStyles } from '@material-ui/core/styles';

import Avatar from '@material-ui/core/Avatar';
import Button from '@material-ui/core/Button';
import TextField from '@material-ui/core/TextField';
import Checkbox from '@material-ui/core/Checkbox';
import Grid from '@material-ui/core/Grid';
import Typography from '@material-ui/core/Typography';
import Container from '@material-ui/core/Container';
import Divider from '@material-ui/core/Divider';
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import ListItemAvatar from '@material-ui/core/ListItemAvatar';
import ListItemText from '@material-ui/core/ListItemText';
import ListItemIcon from '@material-ui/core/ListItemIcon';
import ListItemSecondaryAction from '@material-ui/core/ListItemSecondaryAction';
import LinearProgress from '@material-ui/core/LinearProgress';
import CircularProgress from '@material-ui/core/CircularProgress';
import Paper from '@material-ui/core/Paper';
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableRow from '@material-ui/core/TableRow';
import TableCell from '@material-ui/core/TableCell';

import CloudIcon from '@material-ui/icons/CloudUploadRounded';
import FileIcon from '@material-ui/icons/InsertDriveFileRounded';
import CheckIcon from '@material-ui/icons/CheckRounded';
import StarsIcon from '@material-ui/icons/StarsRounded';
import UploadIcon from '@material-ui/icons/BackupRounded';
import AddIcon from '@material-ui/icons/AddBoxRounded';
import RemoveIcon from '@material-ui/icons/CancelRounded';
import ModifyIcon from '@material-ui/icons/EditRounded';
import ReloadIcon from '@material-ui/icons/SettingsBackupRestoreRounded';

import Radio from '@material-ui/core/Radio';
import RadioGroup from '@material-ui/core/RadioGroup';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import FormControl from '@material-ui/core/FormControl';

import SocketIOClient from 'socket.io-client';
import SocketIOFileClient from 'socket.io-file-client';

import Stepper from './Stepper';
import { ENDPOINT } from '../App';

const useStyles = makeStyles(theme => ({
  paper: {
    display: 'flex',
    flexDirection: 'column',
    alignItems: 'center',
  },
  form: {
    width: '100%', // Fix IE 11 issue
    marginTop: theme.spacing(1),
  },
  input: {
    display: 'none',
  },
  control: {
    marginTop: theme.spacing(2)
  },
  leftIcon: {
    marginRight: theme.spacing(1),
  },
  rightIcon: {
    marginLeft: theme.spacing(1),
  },
  dense: {
    marginTop: theme.spacing(2),
  },
  divider:{
    marginTop: theme.spacing(2),
    marginBottom: theme.spacing(2),
  },
  textTitle: {
    fontWeight: "bold"
  },
  textMessage: {
    margin: theme.spacing(5)
  },
  textSuccess: {
    margin: theme.spacing(5),
    color: "#4CAF50"
  },
  buttonLarge: {
    height: "60px",
    marginTop: theme.spacing(1)
  },
  buttonAction: {
    marginBottom: theme.spacing(3)
  },
  progress: {
    marginTop: theme.spacing(5),
    marginBottom: theme.spacing(5)
  },
  patches: {
    width: "70%",
    maxHeight: "250px",
    overflow: "auto",
    marginTop: theme.spacing(0),
    marginBottom: theme.spacing(4)
  },
  table: {
    width: "70%",
    overflow: "auto"
  },
  colorError: {
    color: "#E91E63"
  },
  colorInfo: {
    color: "#3F51B5"
  },
  colorSuccess: {
    color: "#4CAF50"
  },
  colorPurple: {
    color: "#9C27B0"
  },
  colorPink: {
    color: "#E91E63"
  },
  avatar: {
    width: theme.spacing(7),
    height: theme.spacing(7),
    marginLeft: theme.spacing(1),
    marginRight: theme.spacing(2),
    marginTop: theme.spacing(1),
    marginBottom: theme.spacing(1),
    borderRadius: 0,
  },
}));

export default (props) => {

  const UpdateSteps = {
    Ready: 0,
    Upload: 1,
    Review: 2,
    Update: 3,
    Finish: 4,
  }

  const UpdateErrors = {
    None: 0,
    Unknown: 1,
    UploadError: 2,
    UploadAbort: 3,
    ReviewError: 4,
    UpdateError: 5,
  }

  const PatchType = {
    Added: 0,
    Removed: 1,
    Modified: 2,
  }

  const PatchMode = {
    Full: 0,
    Partial: 1
  }

  const steps = [
    {
      title: 'Prepare',
      content: 'Compress your patches'
    },
    {
      title: 'Upload',
      content: 'Push patches to server'
    },
    {
      title: 'Review',
      content: 'See what changes from your patches'
    },
    {
      title: 'Patch',
      content: 'Apply patches to target package'
    },
    {
      title: 'Done',
      content: 'Grab a beer, enjoy your patches'
    }
  ];

  const avatars = [
    'https://image.flaticon.com/icons/svg/119/119590.svg',
    'https://image.flaticon.com/icons/svg/119/119591.svg',
    'https://image.flaticon.com/icons/svg/119/119592.svg',
    'https://image.flaticon.com/icons/svg/119/119589.svg',
    'https://image.flaticon.com/icons/svg/119/119588.svg',
    'https://image.flaticon.com/icons/svg/119/119587.svg',
    'https://image.flaticon.com/icons/svg/119/119586.svg',
    'https://image.flaticon.com/icons/svg/119/119585.svg',
    'https://image.flaticon.com/icons/svg/119/119584.svg',
    'https://image.flaticon.com/icons/svg/119/119583.svg',
    'https://image.flaticon.com/icons/svg/119/119582.svg',
    'https://image.flaticon.com/icons/svg/119/119581.svg',
    'https://image.flaticon.com/icons/svg/119/119580.svg',
    'https://image.flaticon.com/icons/svg/119/119575.svg'
  ];

  const classes = useStyles(props);
  
  // target game selector

  const [activeGame, setActiveGame] = React.useState(null);
  const [activeGameList, setActiveGameList] = React.useState([]);

  const [selectedGame, setSelectedGame] = React.useState(0);
  const handleGameItemClick = (event, index) => {

    let selectedIndex = Math.min(index, activeGameList.length - 1);

    setSelectedGame(selectedIndex);
    setActiveGame(activeGameList[selectedIndex]);
  }

  React.useEffect(() => {

    let newGameList = props.games.filter(item => item.type === props.category);
    let selectedIndex = Math.min(Math.max(selectedGame, 0), newGameList.length - 1);

    setActiveGameList(newGameList);
    setSelectedGame(selectedIndex);
    setActiveGame(newGameList[selectedIndex]);

  }, [props.category])

  // package game selector

  const [selectedFile, setSelectedFile] = React.useState('');
  const handleFileSelect = (event) => {
    const inputFile = document.getElementById("file-input");
    if (inputFile && inputFile["files"]) {
      const file = inputFile["files"][0];
      if (file) {
        setSelectedFile(file.name);
      }
    }
  }

  // package mode selector

  const [selectedMode, setSelectedMode] = React.useState('');
  const handleModeChange = (event: React.ChangeEvent<HTMLInputElement>) => {
    setSelectedMode((event.target as HTMLInputElement).value);
  }

  // buttons

  const isDone = () => {
    return activeState.step === UpdateSteps.Finish;
  }

  const isError = () => {
    return activeState.error !== UpdateErrors.None;
  }

  const isInputable = () => {
    return !isError() &&
      activeState.step === UpdateSteps.Ready;
  }

  const isUpdatable = () => {
    return isInputable() && selectedFile !== '';
  }

  const isConfirmable = () => {
    return !isError() && activeState.step === UpdateSteps.Review;
  }

  // handle update events

  const fileInputAccepts = 'application/zip, application/x-zip-compressed, multipart/x-zip';

  const [activeSocket, setActiveSocket] = useState(null);
  const [activeState, setActiveState] = useState({
    step: 0,
    error: 0,
    errorMsg: '',
    counterValue: 0,
    counterTotal: 0,
    progress: 0,
    package: null
  });

  const getStepContent = (step) => {
    switch (step.step) {
      case UpdateSteps.Upload:
        return (<LinearProgress color="primary" variant="determinate" value={step.progress || 0} className={classes.progress} />);
      case UpdateSteps.Review:
        return (
          <div className={classes.paper}>
            <Table size="small" className={classes.patches}>
              <TableBody>
                <TableRow>
                  <TableCell align="center">
                    <Typography variant="overline" className={classes.colorSuccess} align="center">
                      <strong>
                        {`Added ${getPatchCountForType(step.package.patches, PatchType.Added)} file(s)`}
                      </strong>
                    </Typography>
                  </TableCell>
                  <TableCell align="center">
                    <Typography variant="overline" className={classes.colorInfo} align="center">
                      <strong>
                        {`Modified ${getPatchCountForType(step.package.patches, PatchType.Modified)} file(s)`}
                      </strong>
                    </Typography>
                  </TableCell>
                  <TableCell align="center">
                    <Typography variant="overline" className={classes.colorError} align="center">
                      <strong>
                        {`Removed ${getPatchCountForType(step.package.patches, PatchType.Removed)} file(s)`}
                      </strong>
                    </Typography>
                  </TableCell>
                </TableRow>
              </TableBody>
            </Table>
            <List dense className={classes.patches}>
              {
                step.package.patches.map((item, index) => {
                  return (
                    <ListItem key={index}>
                      <ListItemIcon><FileIcon /></ListItemIcon>
                      <ListItemText primary={getPatchItemText(item)}/>
                      <ListItemSecondaryAction>
                        <ListItemIcon>{getPatchItemIcon(item.type)}</ListItemIcon>
                      </ListItemSecondaryAction>
                    </ListItem>
                  )
                })
              }
            </List>
            <Grid container>
              <Grid item md={3} sm={12} xs={12}></Grid>
              <Grid item md={6} sm={12} xs={12}>
                <Grid container>
                  <Grid item md={5} sm={12} xs={12}>
                    <Button fullWidth className={classes.buttonAction} variant="contained" color="secondary" component="span" onClick={handleConfirmResetClick}>
                      Reset
                      <ReloadIcon className={classes.rightIcon} />
                    </Button>
                  </Grid>
                  <Grid item md={2} sm={12} xs={12}></Grid>
                  <Grid item md={5} sm={12} xs={12}>
                    <Button fullWidth className={classes.buttonAction} variant="contained" color="primary" component="span" onClick={handleConfirmButtonClick} disabled={!isConfirmable()}>
                      Submit
                      <UploadIcon className={classes.rightIcon} />
                    </Button>
                  </Grid>
                </Grid>
              </Grid>
            </Grid>
          </div>
        )
      case UpdateSteps.Update:
        return (
          <div className={classes.paper}>
            <Typography variant="subtitle1" color="primary" className={classes.textMessage}>
              {`Applying package for ${activeGame ? activeGame.title : ""}...`}
            </Typography>
            <CircularProgress className={classes.progress} />
          </div>
        )
      default:
        return (<div />);
    }
  }

  const getStepError = (step) => {
    return (
      <div className={classes.paper}>
        <Typography variant="subtitle1" color="secondary" className={classes.textMessage}>
          <strong>{step.errorMsg}</strong>
        </Typography>
        <Grid container>
          <Grid item md={4} sm={12} xs={12}></Grid>
          <Grid item md={4} sm={12} xs={12}>
            <Button fullWidth className={classes.buttonAction} variant="contained" color="secondary" component="span" onClick={handleConfirmResetClick}>
              Start over
              <ReloadIcon className={classes.rightIcon} />
            </Button>
          </Grid>
          <Grid item md={4} sm={12} xs={12}></Grid>
        </Grid>
      </div>
    )
  }

  const getStepDone = (step) => {
    return (
      <div className={classes.paper}>
        <Typography variant="subtitle1" className={classes.textSuccess}>
          <strong>Package updated successfull</strong>
        </Typography>
        <Grid container>
          <Grid item md={4} sm={12} xs={12}></Grid>
          <Grid item md={4} sm={12} xs={12}>
            <Button fullWidth className={classes.buttonAction} variant="contained" color="primary" component="span" onClick={handleConfirmResetClick}>
              Update another package
              <ReloadIcon className={classes.rightIcon} />
            </Button>
          </Grid>
          <Grid item md={4} sm={12} xs={12}></Grid>
        </Grid>
      </div>
    )
  }

  const getPatchItemIcon = (type) => {
    switch (type) {
      case PatchType.Added:
        return (<AddIcon className={classes.colorSuccess} />);
      case PatchType.Removed:
          return (<RemoveIcon className={classes.colorError} />);
      case PatchType.Modified:
          return (<ModifyIcon className={classes.colorInfo} />);
      default:
          return (<FileIcon />);
    }
  }

  const getPatchItemText = (patch) => {
    switch (patch.type) {
      case PatchType.Added:
        return patch.source;
      case PatchType.Removed:
      case PatchType.Modified:
        return patch.target;
      default:
        return patch.target;
    }
  }

  const getPatchCountForType = (patches, type) => {
    return patches.reduce((total, item) => total + ((item.type === type) ? 1 : 0), 0);
  }

  const handleUpdateButtonClick = () => {
    const fileInput = document.getElementById('file-input');
    if (fileInput) {
      const socket = SocketIOClient(`${ENDPOINT}/update`, { forceNew: true, reconnection: false });
      socket.on('connect', () => {
        // console.log('Connected to server');
        const uploader = new SocketIOFileClient(socket);
        uploader.on('ready', () => {
          // console.log('Upload ready');
          const { id, name } = activeGame;
          uploader.upload(fileInput, {
            data: {
              owner: localStorage.getItem('nickname'),
              gameId: id,
              gameName: name
            }
          });
        });
        uploader.on('start', (fileInfo) => {
          // console.log('Upload start:', fileInfo);
        });
        uploader.on('stream', (fileInfo) => {
          // console.log(`Upload progress: ${fileInfo.wrote} / ${fileInfo.size} byte(s)`);
        });
        uploader.on('complete', (fileInfo) => {
          // console.log('Upload complete:', fileInfo);
        });
        uploader.on('error', (error) => {
          // console.log('Upload error:', error);
          setActiveState({
            ...activeState,
            error: UpdateErrors.UploadError,
            errorMsg: 'Error occured while uploading package',
          });
        });
        uploader.on('abort', (fileInfo) => {
          // console.log('Upload abort:', fileInfo);
          setActiveState({
            ...activeState,
            error: UpdateErrors.UploadError,
            errorMsg: 'Package uploading has been aborted',
          });
        });
      });

      socket.on('disconnect', (reason) => {
        // console.log('Disconnected from server:', reason);
        setActiveState({
          ...activeState,
          error: UpdateErrors.Unknown,
          errorMsg: 'Disconnected from server',
        });
      });

      socket.on('error', (error) => {
        // console.log('Error with socket:', error);
        setActiveState({
          ...activeState,
          error: UpdateErrors.Unknown,
          errorMsg: 'Socket server error',
        });
      });

      socket.on('update', (payload) => {
        // console.log('Update:', payload);
        if (payload) {
          setActiveState(payload);
        }
      });

      setActiveSocket(socket);
      setActiveState({
        ...activeState,
        step: UpdateSteps.Upload
      });
    }
  }

  const handleConfirmButtonClick = () => {
    if (activeSocket) {
      activeSocket.emit('apply', {
        owner: localStorage.getItem('nickname'),
        packageId: activeState.package.id,
        packageMode: (selectedMode === 'full') ? PatchMode.Full : PatchMode.Partial
      });
      setActiveState({
        ...activeState,
        step: UpdateSteps.Update
      });
    }
  }

  const handleConfirmResetClick = () => {

    if (activeSocket)
      activeSocket.close();

    document.getElementById('file-input')['value'] = '';
    document.getElementById('file-text')['value'] = '';

    setActiveSocket(null);
    setActiveState({
      step: 0,
      error: 0,
      errorMsg: '',
      counterValue: 0,
      counterTotal: 0,
      progress: 0,
      package: null
    });

    setSelectedFile('');
  }

  return (
    <Container component="main" maxWidth="md">
      <CssBaseline />
      <div className={classes.paper}>
        <form className={classes.form} noValidate>
          <Grid container spacing={2}>
            <Grid item xs={12} sm={12}>
              <Typography variant="h6" align="center" className={classes.colorPink}>
                <strong>
                  {activeGame ? activeGame.title : ''}
                </strong>
              </Typography>
              <Typography variant="subtitle2" align="center">
                {activeGame ? `Version ${activeGame.version}` : ''}
              </Typography>
            </Grid>
            <Grid item xs={12} sm={12}>
              <Paper>
                <Stepper steps={steps} stepError={activeState.error} activeStep={activeState.step} />
                {
                  (isError()) ? getStepError(activeState) : ((isDone()) ? getStepDone(activeState) : getStepContent(activeState))
                }
              </Paper>
            </Grid>
            <Grid item xs={12} sm={6}>
              <Typography variant="body1" display="block" gutterBottom>
                <strong>Target package</strong>
              </Typography>
              <Typography variant="caption" display="block" gutterBottom>
                Choose the patches coressponding with package
              </Typography>
              <List component="nav">
                {
                  activeGameList.map((game, index) => {
                    return (
                      <ListItem dense button key={index} disabled={!isInputable()} selected={selectedGame === index} onClick={event => handleGameItemClick(event, index)}>
                        <ListItemAvatar>
                          <Avatar src={(game.image !== '') ? game.image : avatars[index % avatars.length]} className={classes.avatar} />
                        </ListItemAvatar>
                        <ListItemText>
                          <Typography variant="body1"><strong>{game.title}</strong></Typography>
                          <Typography variant="caption">{`Version: ${game.version}`}</Typography>
                        </ListItemText>
                        <ListItemSecondaryAction>
                          <Checkbox edge="start" icon={(game.type === 0) ? (<StarsIcon />) : (<div />)} checkedIcon={<CheckIcon />} checked={selectedGame === index} tabIndex={-1} disableRipple/>
                        </ListItemSecondaryAction>
                      </ListItem>
                    )
                  })
                }
              </List>
            </Grid>
            <Grid item xs={12} sm={6}>
              <Typography variant="body1" display="block" gutterBottom>
                <strong>Patch package</strong>
              </Typography>
              <Typography variant="caption" display="block" gutterBottom>
                Support .zip file, try to compress at high level for faster uploading
              </Typography>
              <TextField fullWidth disabled placeholder="Choose file" id="file-text" margin="dense" variant="outlined" value={selectedFile} />
              <input accept={fileInputAccepts} className={classes.input} id="file-input" type="file" onChange={handleFileSelect} />
              <label htmlFor="file-input">
                <Button fullWidth variant="contained" color="secondary" component="span" className={classes.control} disabled={!isInputable()}>
                  Choose file
                  <FileIcon className={classes.rightIcon} />
                </Button>
              </label>
              <Divider className={classes.divider} />
              <Typography variant="body1" display="block" gutterBottom>
                <strong>Patch mode</strong>
              </Typography>
              <FormControl component="fieldset">
                <RadioGroup row aria-label="position" name="position" defaultValue="full" onChange={handleModeChange}>
                  <FormControlLabel value="full" control={<Radio color="primary" />} label="Full" labelPlacement="end" />
                  <FormControlLabel value="part" control={<Radio color="primary" />} label="Partial" labelPlacement="end" />
                </RadioGroup>
              </FormControl>
              <Divider className={classes.divider} />
              <Button fullWidth size="large" variant="contained" color="primary" component="span" className={classes.buttonLarge} onClick={handleUpdateButtonClick} disabled={!isUpdatable()}>
                Update
                <CloudIcon className={classes.rightIcon} />
              </Button>
            </Grid>
          </Grid>
        </form>
      </div>
    </Container>
  );
}