import React from 'react';

import { makeStyles } from '@material-ui/core/styles';

import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import ListItemIcon from '@material-ui/core/ListItemIcon';
import ListItemText from '@material-ui/core/ListItemText';

import Collapse from '@material-ui/core/Collapse';
import ExpandLess from '@material-ui/icons/ExpandLess';
import ExpandMore from '@material-ui/icons/ExpandMore';

import { Avatar } from '@material-ui/core';

const useStyles = makeStyles(theme => ({
  root: {
    width: '100%',
    maxWidth: 360,
    backgroundColor: theme.palette.background.paper,
  },
  nested: {
    paddingLeft: theme.spacing(4),
  },
  small: {
    width: theme.spacing(3),
    height: theme.spacing(3),
    borderRadius: 0,
  },
  large: {
    width: theme.spacing(7),
    height: theme.spacing(7),
    borderRadius: 0,
  },
}));

export default (props) => {

  const classes = useStyles(props);

  const [open, setOpen] = React.useState(true);

  const handleClick = () => {
    setOpen(!open);
  }

  const handleClickNestedItem = (event, parentIndex, index) => {
    props.onChildSelected(event, index);
    props.onSelected(event, parentIndex);
  }

  const getItemIcon = (activeItem) => {
    switch (activeItem) {
      case 0:
        return (
          <Avatar src="./images/cube.png" className={classes.small} />
        );
      case 1:
        return (
          <Avatar src="./images/event.png" className={classes.small} />
        );
      case 2:
        return (
          <Avatar src="./images/cog.png" className={classes.small} />
        );
      case 3:
        return (
          <Avatar src="./images/face.png" className={classes.small} />
        );
    }
  };

  const getItemGameIcon = (activeItem) => {
    switch (activeItem) {
      case 0:
        return (
          <Avatar src="./images/microchip.png" className={classes.small} />
        );
      case 1:
        return (
          <Avatar src="./images/cards.png" className={classes.small} />
        );
      case 2:
        return (
          <Avatar src="./images/joystick.png" className={classes.small} />
        );
      case 3:
        return (
          <Avatar src="./images/party.png" className={classes.small} />
        );
      case 4:
        return (
          <Avatar src="./images/gameplay.png" className={classes.small} />
        );
      case 5:
        return (
          <Avatar src="./images/gameplay.png" className={classes.small} />
        );
    }
  };

  const getItemExpander = (items, parentIndex, open) => {
    return (
      <div>
        {
          items.map((item, index) => {
            return (
              <Collapse key={index} in={open} timeout="auto" unmountOnExit>
                <List component="div" disablePadding>
                  <ListItem key={index} button className={classes.nested} selected={props.activeItem === parentIndex && props.activeChildItem === index} onClick={event => handleClickNestedItem(event, parentIndex, index)}>
                    <ListItemIcon>
                      {getItemGameIcon(index)}
                    </ListItemIcon>
                    <ListItemText primary={item.title} />
                  </ListItem>
                </List>
              </Collapse>
            )
          })
        }
      </div>
    )
  };

  const getItemNormal = (item, index) => {
    return (
      <ListItem key={index} button selected={props.activeItem === index} onClick={event => props.onSelected(event, index)}>
        <ListItemIcon>
        {getItemIcon(index)}
        </ListItemIcon>
        <ListItemText primary={item.title} />
      </ListItem>
    )
  };

  const getItemWithNested = (item, index) => {
    return (
      <div key={index}>
        <ListItem key={index} button selected={props.activeItem === index} onClick={handleClick}>
          <ListItemIcon>
          {getItemIcon(index)}
          </ListItemIcon>
          <ListItemText primary={item.title} />
          {open ? <ExpandLess /> : <ExpandMore />}
        </ListItem>
        {getItemExpander(item.items, index, open)}
      </div>
    )
  };

  return (
    <List>
      {
        props.items.map((item, index) => {
          return (
            <div key={index}>
              {(item.items) ? getItemWithNested(item, index) : getItemNormal(item, index)}
            </div>
          )
        })
      }
    </List>
  );
}
