import React, { useState, useEffect } from 'react';
import { makeStyles } from '@material-ui/core/styles';

import SocketIOClient from 'socket.io-client';

import PageMain from './pages/PageMain';
import PageLogin from './pages/PageLogin';
import PageLoading from './pages/PageLoading';
import PageRegister from './pages/PageRegister';

export const LOGIN = window['config'].login || false;
export const ENDPOINT = window['config'].endpoint || '0.0.0.0';

const useStyles = makeStyles(theme => ({
  messages: {
    display: 'flex',
    flexDirection: 'column',
    alignItems: 'center',
    margin: theme.spacing(4)
  }
}));

export default (props) => {

  const classes = useStyles(props);

  const getLoginData = () => {
    return {
      token: localStorage.getItem('token')
    }
  }

  const willRelogin = () => {
    const { token } = getLoginData();
    return token !== null && token !== '';
  }

  const [isLoading, setIsLoading] = useState(willRelogin());
  const [isLoggedIn, setIsLoggedIn] = useState(false);

  const [loginState, setLoginState] = useState({});
  const [authenPage, setAuthenPage] = useState('login');

  useEffect(() => {

    localStorage.removeItem('username');
    localStorage.removeItem('password');

    if (LOGIN === true) {
      const { token } = getLoginData();
      if (token !== null && token !== '') {
  
        const socket = SocketIOClient(`${ENDPOINT}/authen`, { reconnection: true });
        socket.on('connect', () => {
          socket.emit('login', {
            token: token
          });
        });
    
        socket.on('disconnect', (reason) => {
          setLoginState({
            ...loginState,
            error: 'Disconnected from server',
          });
        });
    
        socket.on('error', (error) => {
          setLoginState({
            ...loginState,
            error: 'Error connecting with server',
          });
        });
    
        socket.on('login', (payload) => {

          if (payload.error) {
            handleLogout();
            return;
          }

          localStorage.setItem('nickname', payload.data.nickname);

          setIsLoading(false);
          setIsLoggedIn(!payload.error);
          
        });
      }
    }
  }, []);

  const handleAuthenSuccess = () => {
    setIsLoading(false);
    setIsLoggedIn(true);
  }

  const handleLogout = () => {

    localStorage.clear();

    setIsLoading(willRelogin());
    setIsLoggedIn(false);
  }

  const getAuthenPage = () => {
    return (authenPage === 'login') ? (<PageLogin onSwitched={setAuthenPage} onSuccess={handleAuthenSuccess} />) :
      (<PageRegister onSwitched={setAuthenPage} onSuccess={handleAuthenSuccess} />);
  }

  const getContent = () => {
    if (isLoading) {
      return (<PageLoading message="Connecting..." />);
    }
    else {
      return (isLoggedIn) ? (<PageMain onLogout={handleLogout} />) : getAuthenPage();
    }
  }

  return (LOGIN === true) ? getContent() : (<PageMain />);
}