import React, { useState } from 'react';
import clsx from 'clsx';

import { makeStyles } from '@material-ui/core/styles';
import CssBaseline from '@material-ui/core/CssBaseline';

import Drawer from '@material-ui/core/Drawer';
import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';
import Typography from '@material-ui/core/Typography';
import Divider from '@material-ui/core/Divider';
import IconButton from '@material-ui/core/IconButton';
import Container from '@material-ui/core/Container';
import Menu from '@material-ui/core/Menu';
import MenuItem from '@material-ui/core/MenuItem';

import MenuIcon from '@material-ui/icons/Menu';
import StackIcon from '@material-ui/icons/ChevronLeft';
import PeopleIcon from '@material-ui/icons/PeopleRounded';
import AccountIcon from '@material-ui/icons/AccountCircleRounded';
import ExitIcon from '@material-ui/icons/ExitToAppRounded';

import Footer from '../components/Footer';
import SideBar from '../components/SideBar';

import TabUser from '../components/TabUser';
import TabPackage from '../components/TabPackage';
import TabActivity from '../components/TabActivity';
import TabSetting from '../components/TabSetting';

const drawerWidth = 240;

const useStyles = makeStyles(theme => ({
  root: {
    display: 'flex',
  },
  toolbar: {
    paddingRight: 24, // keep right padding when drawer closed
  },
  toolbarIcon: {
    display: 'flex',
    alignItems: 'center',
    justifyContent: 'flex-end',
    padding: '0 8px',
    ...theme.mixins.toolbar,
  },
  appBar: {
    zIndex: theme.zIndex.drawer + 1,
    transition: theme.transitions.create(['width', 'margin'], {
      easing: theme.transitions.easing.sharp,
      duration: theme.transitions.duration.leavingScreen,
    }),
    background: '#5f27cd',
  },
  appBarShift: {
    marginLeft: drawerWidth,
    width: `calc(100% - ${drawerWidth}px)`,
    transition: theme.transitions.create(['width', 'margin'], {
      easing: theme.transitions.easing.sharp,
      duration: theme.transitions.duration.enteringScreen,
    }),
  },
  menuButton: {
    marginRight: 36,
  },
  menuButtonHidden: {
    display: 'none',
  },
  title: {
    flexGrow: 1,
  },
  drawerPaper: {
    position: 'relative',
    whiteSpace: 'nowrap',
    width: drawerWidth,
    transition: theme.transitions.create('width', {
      easing: theme.transitions.easing.sharp,
      duration: theme.transitions.duration.enteringScreen,
    }),
  },
  drawerPaperClose: {
    overflowX: 'hidden',
    transition: theme.transitions.create('width', {
      easing: theme.transitions.easing.sharp,
      duration: theme.transitions.duration.leavingScreen,
    }),
    width: theme.spacing(7),
    [theme.breakpoints.up('sm')]: {
      width: theme.spacing(9),
    },
  },
  appBarSpacer: theme.mixins.toolbar,
  content: {
    flexGrow: 1,
    height: '100vh',
    overflow: 'auto',
  },
  container: {
    paddingTop: theme.spacing(4),
    paddingBottom: theme.spacing(4),
  },
  paper: {
    padding: theme.spacing(2),
    display: 'flex',
    overflow: 'auto',
    flexDirection: 'column',
  },
  fixedHeight: {
    height: 240,
  },
  margin: {
    marginLeft: theme.spacing(0.5),
    marginRight: theme.spacing(0.5)
  },
}));

export default (props) => {

  const classes = useStyles(props);

  const [activeTab, setActiveTab] = useState(0);
  const [activeChildTab, setActiveChildTab] = useState(0);

  const [isDrawerOpened, setDrawerOpened] = useState(true);

  const [anchorEl, setAnchorEl] = React.useState<null | HTMLElement>(null);

  const tabs = [
    {
      title: 'Packages',
      description: 'Manage all available packages',
      items: [
        {
          title: 'Core',
          description: 'Core packages'
        },
        {
          title: 'Card',
          description: 'Card games'
        },
        {
          title: 'Slots',
          description: 'Slot machine games'
        },
        {
          title: 'Betting',
          description: 'Betting games'
        },
        {
          title: 'Casual',
          description: 'Casual games'
        },
        {
          title: 'Fishing',
          description: 'Fishing games'
        },
      ]
    },
    {
      title: 'Activities',
      description: 'View history about package patching'
    },
    {
      title: 'Settings',
      description: 'Edit portal system configurations'
    },
    {
      title: 'Users',
      description: 'Manage users and permissions'
    },
  ];

  const handleDrawerOpen = () => {
    setDrawerOpened(true);
  };

  const handleDrawerClose = () => {
    setDrawerOpened(false);
  };

  const handleTabSelected = (event, selectedItem) => {
    setActiveTab(selectedItem);
  }

  const handleChildTabSelected = (event, selectedItem) => {
    setActiveChildTab(selectedItem);
  }

  const handleMenuClick = (event: React.MouseEvent<HTMLButtonElement>) => {
    setAnchorEl(event.currentTarget);
  }

  const handleMenuClose = () => {
    setAnchorEl(null);
  }

  const handleMenuProfileClick = () => {

  }

  const handleMenuLogoutClick = () => {
    props.onLogout && props.onLogout();
  }

  const getActiveTab = (activeTab, activeChildTab) => {
    switch (activeTab) {
      case 0:
        return (<TabPackage games={props.games} category={activeChildTab} />);
      case 1:
        return (<TabActivity />);
      case 2:
        return (<TabSetting />);
      case 3:
        return (<TabUser />);
    }
  }

  const getActiveUserName = () => {
    return localStorage.getItem('nickname') || 'Guest';
  }

  return (
    <div className={classes.root}>
      <CssBaseline />
      <AppBar position="absolute" className={clsx(classes.appBar, isDrawerOpened && classes.appBarShift)}>
        <Toolbar className={classes.toolbar}>
          <IconButton
            edge="start"
            color="inherit"
            aria-label="Open drawer"
            onClick={handleDrawerOpen}
            className={clsx(classes.menuButton, isDrawerOpened && classes.menuButtonHidden)} >
            <MenuIcon />
          </IconButton>
          <Typography variant="body1" color="inherit" noWrap className={classes.title}>
            <strong>{tabs[activeTab].title}</strong>
            <Typography variant="caption" display="block" gutterBottom>
              {tabs[activeTab].description}
            </Typography>
          </Typography>
          <Typography variant="subtitle1" color="inherit" noWrap className={classes.margin}>
            <strong>{getActiveUserName()}</strong>
          </Typography>
          <IconButton color="inherit" aria-controls="user-menu" aria-haspopup="true" onClick={handleMenuClick}>
            <PeopleIcon />
          </IconButton>
          <Menu id="user-menu" anchorEl={anchorEl} keepMounted open={Boolean(anchorEl)} onClose={handleMenuClose}>
            <MenuItem onClick={handleMenuProfileClick}>
              <AccountIcon color="inherit" style={{ marginRight: 7 }} /> Profile
            </MenuItem>
            <MenuItem onClick={handleMenuLogoutClick}>
              <ExitIcon color="inherit" style={{ marginRight: 7 }} /> Logout
            </MenuItem>
          </Menu>
        </Toolbar>
      </AppBar>
      <Drawer
        variant="permanent"
        classes={{ paper: clsx(classes.drawerPaper, !isDrawerOpened && classes.drawerPaperClose) }}
        open={isDrawerOpened} >
        <div className={classes.toolbarIcon}>
          <IconButton onClick={handleDrawerClose}>
            <StackIcon />
          </IconButton>
        </div>
        <Divider />
        <SideBar items={tabs} activeItem={activeTab} activeChildItem={activeChildTab} onSelected={handleTabSelected} onChildSelected={handleChildTabSelected}/>
      </Drawer>
      <main className={classes.content}>
        <div className={classes.appBarSpacer} />
        <Container maxWidth="lg" className={classes.container}>
          {getActiveTab(activeTab, activeChildTab)}
        </Container>
        <Footer />
      </main>
    </div>
  );
}