import React, { useState, useEffect } from 'react';
import SocketIOClient from 'socket.io-client';

import CssBaseline from '@material-ui/core/CssBaseline';
import { makeStyles } from '@material-ui/core/styles';

import Container from '@material-ui/core/Container';
import Button from '@material-ui/core/Button';

import Snackbar from '@material-ui/core/Snackbar';
import SnackbarContent from '@material-ui/core/SnackbarContent';

import Chip from '@material-ui/core/Chip';
import FaceIcon from '@material-ui/icons/FaceRounded';
import GameIcon from '@material-ui/icons/SportsEsportsRounded';
import ClockIcon from '@material-ui/icons/QueryBuilderRounded';

import { Grid } from '@material-ui/core';

import { ENDPOINT } from '../App';

interface IAction {
  owner: string;
  asset: string;
  time: number;
}

const useStyles = makeStyles(theme => ({
  '@global': {
    body: {
      backgroundColor: theme.palette.common.white,
    },
  },
  paper: {
    marginTop: theme.spacing(8),
    display: 'flex',
    flexDirection: 'column',
    alignItems: 'center',
  },
  record: {
    width: '100%',
    '& > * + *': {
      marginTop: theme.spacing(2),
    },
    marginTop: theme.spacing(2)
  },
  avatar: {
    margin: theme.spacing(1),
    backgroundColor: theme.palette.secondary.main,
  },
  form: {
    width: '100%', // Fix IE 11 issue.
    marginTop: theme.spacing(1),
  },
  submit: {
    margin: theme.spacing(3, 0, 2),
  },
  messages: {
    display: 'flex',
    flexDirection: 'column',
    alignItems: 'center',
    margin: theme.spacing(4)
  },
  grid: {
    margin: theme.spacing(1)
  }
}));

export default (props) => {

  const classes = useStyles(props);
  
  const [actions, setActions] = useState([]);
  const [initialized, setInitialized] = useState(false);

  const getContent = (action: IAction) => {

    return (
      <Grid
        container
        direction="row"
        justify="space-evenly"
        alignItems="flex-start"
      >
        <Chip
          icon={<ClockIcon />}
          label={`${new Date(action.time).toLocaleDateString()} ${new Date(action.time).toLocaleTimeString()}`}
          className={classes.grid}
        />
        <Chip
          icon={<FaceIcon />}
          label={action.owner}
          color="primary"
          className={classes.grid}
        />
        <Chip
          icon={<GameIcon />}
          label={action.asset}
          color="secondary"
          className={classes.grid}
        />
      </Grid>
    )
  };

  const button = (
    <Button color="secondary" size="small">
      Detail
    </Button>
  );

  const render = () => {
    return (actions || []).map((action) => {
      return (
        <div className={classes.record}>
          <SnackbarContent message={getContent(action)} action={button} />
        </div>
      );
    });
  }

  const connect = () => {

    if (initialized)
      return;

    const socket = SocketIOClient(`${ENDPOINT}/actions`, { reconnection: true });
    socket.on('connect', () => {
      console.log("ACT.connected");
      setInitialized(true);
    });

    socket.on('disconnect', (reason) => {
      console.log("ACT.connected");
      setInitialized(false);
      connect();
    });

    socket.on('error', (error) => {
      console.log("ACT.error");
      setInitialized(false);
      connect();
    });

    socket.on('first', (payload) => {
      console.log("ACT.first", payload);
      setActions(payload.sort((a, b) => b.time - a.time));
      setInitialized(true);
    });

    socket.on('update', (payload) => {
      console.log("ACT.update", payload);
      setActions(payload.sort((a, b) => b.time - a.time));
      setInitialized(true);
    });
  };

  useEffect(() => {
    connect();
  });

  return render();
}