import React, { useState } from 'react';
import SocketIOClient from 'socket.io-client';

import CssBaseline from '@material-ui/core/CssBaseline';
import { makeStyles } from '@material-ui/core/styles';

import Avatar from '@material-ui/core/Avatar';
import Button from '@material-ui/core/Button';
import TextField from '@material-ui/core/TextField';
import Link from '@material-ui/core/Link';
import Grid from '@material-ui/core/Grid';
import Container from '@material-ui/core/Container';
import Typography from '@material-ui/core/Typography';
import FormHelperText from '@material-ui/core/FormHelperText';
import Snackbar from '@material-ui/core/Snackbar';

import RegisterIcon from '@material-ui/icons/AccountBoxRounded';

import { ENDPOINT } from '../App';
import MessageBar from '../components/MessageBar';

const useStyles = makeStyles(theme => ({
  '@global': {
    body: {
      backgroundColor: theme.palette.common.white,
    },
  },
  paper: {
    marginTop: theme.spacing(8),
    display: 'flex',
    flexDirection: 'column',
    alignItems: 'center',
  },
  avatar: {
    margin: theme.spacing(1),
    backgroundColor: theme.palette.secondary.main,
  },
  form: {
    width: '100%', // Fix IE 11 issue.
    marginTop: theme.spacing(3),
  },
  submit: {
    margin: theme.spacing(3, 0, 2),
  },
  messages: {
    display: 'flex',
    flexDirection: 'column',
    alignItems: 'center',
    margin: theme.spacing(4)
  }
}));

export default (props) => {

  const classes = useStyles(props);

  const [username, setUsername] = useState('');
  const [usernameError, setUsernameError] = useState('');

  const [password, setPassword] = useState('');
  const [passwordError, setPasswordError] = useState('');

  const [repassword, setRepassword] = useState('');
  const [repasswordError, setRepasswordError] = useState('');

  const [code, setCode] = useState('');
  const [codeError, setCodeError] = useState('');

  const [result, setResult] = useState(null);
  const [messageVisible, setMessageVisible] = useState(false);

  const isInputValid = () => {
    return validateInput(username) === '' && validateInput(password) === '' && validateInput(repassword) === ''
      && password === repassword;
  }

  const validateInput = (input, name = null) => {
    if (input === '') {
      return `${name} must not be empty`;
    }
    else if (input.length < 5 || input.length > 20) {
      return `${name} must contains 5-20 characters`;
    }
    else if (!/^[a-zA-Z0-9]+$/.test(input)) {
      return `Only characters A-Z, a-z, 0-9 are acceptable.`;
    }
    return '';
  }

  const validdateCode = (input) => {
    if (input === '') {
      return `Code must not be empty`;
    }
  }

  const register = (username, password) => {

    const socket = SocketIOClient(`${ENDPOINT}/authen`, { reconnection: true });
    socket.on('connect', () => {
      socket.emit('register', {
        username: username,
        password: password,
        code: code
      });
    });

    socket.on('disconnect', (reason) => {
      showMessageError('Error occured while registering');
    });

    socket.on('error', (error) => {
      showMessageError('Error occured while registering');
    });

    socket.on('register', (payload) => {
      if (payload.error !== undefined) {
        showMessageError(payload.message);
      }
      else {

        localStorage.setItem('nickname', payload.data.nickname);
        localStorage.setItem('token', payload.data.token);
        
        showMessageSuccess('Register successfull\nRedirecting to home page...');
        props.onSuccess && props.onSuccess();
      }
    });
  }

  const handleUsernameChanged = (event) => {
    setUsername(event.target.value);
    setUsernameError(validateInput(event.target.value, 'Username'));
  }

  const handlePasswordChanged = (event) => {
    setPassword(event.target.value);
    setPasswordError(validateInput(event.target.value, 'Password'));
  }

  const handleRepasswordChanged = (event) => {

    setRepassword(event.target.value);
    setRepasswordError(validateInput(event.target.value, 'Password'));

    if (password !== event.target.value) {
      setRepasswordError('Password confirm must matched with password');
    }
  }

  const handleCodeChanged = (event) => {
    setCode(event.target.value);
    setCodeError(validdateCode(event.target.value));
  }

  const handleSubmit = (event) => {
    event.preventDefault();
    if (isInputValid()) {
      register(username, password);
    }
    else {
      showMessageError('Invalid username or password');
    }
  }

  const showMessageError = (message) => {
    setResult({ error: message });
    setMessageVisible(true);
  }

  const showMessageSuccess= (message) => {
    setResult({ message: message });
    setMessageVisible(true);
  }

  const handleMessageClose = (event, reason) => {

    if (reason === 'clickaway')
      return;

    setMessageVisible(false);
  }

  const getMessageIcon = () => {
    return (result === null) ? '' : ((result.error) ? 'error' : 'success');
  }

  const getMessageText = () => {
    return (result === null) ? '' : ((result.error) ? result.error : 'User register successfull');
  }

  const getMessages = () => {
    return (
      <div className={classes.messages}>
        <Snackbar
          anchorOrigin={{ vertical: 'top', horizontal: 'center' }}
          open={messageVisible}
          autoHideDuration={3000}
          onClose={handleMessageClose}
        >
          <MessageBar variant={getMessageIcon()} message={getMessageText()} onClose={handleMessageClose} />
        </Snackbar>
      </div>
    )
  }

  const resetInput = () => {

    setUsername('');
    setPassword('');
    setRepassword('');
    
    setUsernameError('');
    setPasswordError('');
    setRepasswordError('');

    document.getElementById('username')['value'] = '';
    document.getElementById('password')['value'] = '';
    document.getElementById('repassword')['value'] = '';

    document.getElementById('username').focus();
  }

  return (
    <Container component="main" maxWidth="xs">
      <CssBaseline />
      <div className={classes.paper}>
        {getMessages()}
        <Avatar className={classes.avatar}>
          <RegisterIcon />
        </Avatar>
        <Typography component="h1" variant="h5">
          Register
        </Typography>
        <form className={classes.form} noValidate onSubmit={handleSubmit}>
          <Grid container spacing={2}>
            <Grid item xs={12}>
              <TextField
                variant="outlined"
                required
                fullWidth
                id="username"
                label="Username"
                onChange={handleUsernameChanged}
              />
              <FormHelperText error={usernameError !== ''}>{usernameError}</FormHelperText>
            </Grid>
            <Grid item xs={12}>
              <TextField
                variant="outlined"
                required
                fullWidth
                label="Password"
                type="password"
                id="password"
                onChange={handlePasswordChanged}
              />
              <FormHelperText error={passwordError !== ''}>{passwordError}</FormHelperText>
            </Grid>
            <Grid item xs={12}>
              <TextField
                variant="outlined"
                required
                fullWidth
                label="Retype password"
                type="password"
                id="repassword"
                onChange={handleRepasswordChanged}
              />
              <FormHelperText error={repasswordError !== ''}>{repasswordError}</FormHelperText>
            </Grid>
            <Grid item xs={12}>
              <TextField
                variant="outlined"
                required
                fullWidth
                label="Registration code"
                id="code"
                onChange={handleCodeChanged}
              />
              <FormHelperText error={codeError !== ''}>{codeError}</FormHelperText>
            </Grid>
          </Grid>
          <Button
            type="submit"
            fullWidth
            variant="contained"
            color="primary"
            className={classes.submit}
            disabled={!isInputValid()}
          >
            Register
          </Button>
          <Grid container justify="flex-end">
            <Grid item>
              <Link component="button" variant="body2" onClick={() => { props.onSwitched('login') }}>
                Already have an account ? Login
              </Link>
            </Grid>
          </Grid>
        </form>
      </div>
    </Container>
  )
}