import React from 'react';

import Step from '@material-ui/core/Step';
import Stepper from '@material-ui/core/Stepper';
import StepButton from '@material-ui/core/StepButton';
import Typography from '@material-ui/core/Typography';

import { makeStyles, Theme, createStyles } from '@material-ui/core/styles';

const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    root: {
      width: '100%',
    },
    button: {
      marginRight: theme.spacing(1),
    },
    backButton: {
      marginRight: theme.spacing(1),
    },
    completed: {
      display: 'inline-block',
    },
    instructions: {
      marginTop: theme.spacing(1),
      marginBottom: theme.spacing(1),
    },
  }),
);

export default (props) => {

  const classes = useStyles(props);

  const isError = () => {
    return props.stepError !== 0;
  }

  const isCompleted = (stepIndex) => {
    if (stepIndex === props.activeStep)
      return !isError() && (props.activeStep > stepIndex || props.activeStep >= props.steps.length - 1);
    else
      return props.activeStep > stepIndex || props.activeStep >= props.steps.length - 1;
  }

  return (
    <div className={classes.root}>
      <Stepper alternativeLabel nonLinear activeStep={props.activeStep}>
        {
          props.steps.map((step, index) => {
            return (
              <Step key={index}>
                <StepButton completed={isCompleted(index)} optional={step.content}>
                  {step.title}
                </StepButton>
              </Step>
            );
          })
        }
      </Stepper>
    </div>
  );
}